  position: absolute;
  z-index: 2000;
  background-color: rgba(255, 255, 255, 0.9);
  margin: 0;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  transition: opacity 0.3s; }
  .el-loading-mask.is-fullscreen {
    position: fixed; }
    .el-loading-mask.is-fullscreen .el-loading-spinner {
      margin-top: -25px; }
